<template>
  <div>
    <div class="div-top">
      <span class="title">单耗对标分析</span>
      <div class="btn-group">
        <el-button-group>
          <el-button type="small" :class="{'el-button--primary':btnflag==0}" @click="btnclick(0)" >行业</el-button>
          <el-button type="small" :class="{'el-button--primary':btnflag==1}" @click="btnclick(1)">用户</el-button>
          <el-button type="small" :class="{'el-button--primary':btnflag==2}" @click="btnclick(2)">产品</el-button>
          
        </el-button-group>
      </div>
    </div>
    <div class="div-content">
      <div class="div-frist">
        <span>选择周期</span>
        <el-select style="width: 96px;"  v-model="datetype">
            <el-option 
              v-for="item in cycleoptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
        </el-select>
        <span style="margin-left: 10px;margin-right: 2px;">选择时间</span>
        <el-date-picker v-show="datetype=='year'" v-model="selectedyear" type="year" format="yyyy" value-format="yyyy"  placeholder="选择年"></el-date-picker>
        <el-date-picker v-show="datetype=='month'" v-model="selectedmonth" type="month" format="yyyy-MM" value-format="yyyy-MM"  placeholder="选择月"></el-date-picker>
        <el-date-picker v-show="datetype=='day'" v-model="selecteddate"  type="datetimerange" range-separator="至" start-placeholder="开始时间"  end-placeholder="结束时间" format="yyyy-MM-dd hh:mm:ss" value-format="yyyy-MM-dd hh:mm:ss"></el-date-picker>

        <span style="margin-left: 10px;margin-right: 2px;">车间单元</span>
        <el-select style="width: 96px;"  v-model="workshop">
          <el-option
            v-for="item in workshopoptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-button style="float: right;margin-right: 20px;" type="small" icon="el-icon-search" @click="search"></el-button>
      </div>
      <div class="div-second" >
        <div id="chart_energy" style="width: 100%;height:100%;"></div>
      </div>
      <div class="div-third">
       <template v-if="tableData">
        <el-table
          :data="tableData"
          border
          style="width: 100%;">
          <el-table-column class="table-column"
            prop="name"
            label="类目"
            height="53"
           >
            <template slot-scope="scope">
              <div class="scope-icon"></div>
              <span style="margin-left: 10px;float: left;">{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column  v-for="(item,index) in tableColumn" :key="index"
            :prop="item.prop"
            :label="item.lable"
               height="53">
          </el-table-column>
        </el-table>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  export default {
    data() {
      return {
        treeflag:false,//左侧是否加载树
        btnflag:0,
        cycleoptions: [
          {value: 'day',label: '天'},
          {value: 'month',label: '月'},
          {value: 'year',label: '年' }
        ],
        datetype:"year",
        selectedyear:"",
        selectedmonth:"",
        selecteddate:'',
        currentDate:'',
        workshopoptions:[
          {
            value: 'full',
            label: '全车间'
          },
          {
            value: 'one',
            label: '单车间'
          },
        ],
        workshop:'',
        second_height:400,
        third_height:400,
        divheight:600,
        tableColumn:[
          {
            lable:'2016年1月',
            prop:'201601',
          },
          {
            lable:'2016年2月',
            prop:'201602',
          },
          {
            lable:'2016年3月',
            prop:'201603',
          },
          {
            lable:'2016年4月',
            prop:'201604',
          }
          ,
          {
            lable:'2016年5月',
            prop:'201605',
          },
          {
            lable:'2016年6月',
            prop:'201606',
          }
        ],
        tableData: null,
      };
    },
    components: {},
    mounted() {
      let  that=this;

      var d = new Date();
      var year = d.getFullYear();
      var month = d.getMonth() + 1;
      var day=d.getDate();
      day=day<10?'0'+day:day;
      this.selectedyear=year.toString();
      this.selectedmonth=year+'-'+month;
      this.currentDate=year+'-'+month;//当月


      // that.autoHeight();
      that.getstandanalysisdata();
      that.getechartdata();
    },
    methods: {
      autoHeight() {
        let  that=this;
        var height = document.body.clientHeight-200;
        that.divheight=height;
        that.second_height=height*0.42;
        that.third_height=height*0.42;

      },
      getstandanalysisdata(){//获取table数据
           var that = this;
          
           var params={
             currentDate:this.currentDate
           }
            that.$http
            .get(this.BASE_INFO.BASE_URL+"api/cost/getstandanalysisdata")
            .then(function(result) {
                result=result.data;
                console.dir(result.data);
                 that.tableData=result.data;
               
            });
      },
      getechartdata(){//获取mock echart动态数据
          var params={
               datetype:this.datetype//时间类型
           };
           if(this.datetype=='year'){
               params.date=this.selectedyear;
           }else if(this.datetype=='month'){
               params.date=this.selectedmonth;
           }else if(this.datetype=='day'){
               params.date=this.selecteddate;
           }
         var that = this;
            that.$http
            .post(this.BASE_INFO.BASE_URL+"api/cost/getstandanalysischart",params)
            .then(function(result) {
                result=result.data;
                
                 that.initchart_energy(result.data);
              
            });

      },
      search(){//查询
         this.getstandanalysisdata();
         this.getechartdata();
      },
      //能耗对标
      initchart_energy(data){
         var option = {
           tooltip: {
             trigger: 'axis',
             axisPointer: {
               type: 'cross',
               crossStyle: {
                 color: '#999'
               }
             }
           },
           legend: {
             data:['能耗','成本']
           },
           xAxis: [
             {
               type: 'category',
               splitLine: {show:false},
               data : data.xdata
             }
           ],
           yAxis: [
             {
               type: 'value',
               name: '能耗单位:kW.h',
               min: 0,
               max: 250,
               interval: 50,
               axisLabel: {
                 formatter: '{value}'
               }
             },
             {
               type: 'value',
               name: '对标标准单位:kW.h',
               min: 0,
               max: 25,
               interval: 5,
               axisLabel: {
                 formatter: '{value}'
               }
             }
           ],
           series: [
             {
               name:'能耗',
               type:'bar',
              // color: 'rgba(110,139,254,1)',
               itemStyle: {
                 normal: {
                   color: new echarts.graphic.LinearGradient(
                     0, 0, 0, 1,
                     [
                       {offset: 0, color: '#83bff6'},
                       {offset: 0.5, color: '#188df0'},
                       {offset: 1, color: '#188df0'}
                     ]
                   )
                 },
                 emphasis: {
                   color: new echarts.graphic.LinearGradient(
                     0, 0, 0, 1,
                     [
                       {offset: 0, color: '#2378f7'},
                       {offset: 0.7, color: '#2378f7'},
                       {offset: 1, color: '#83bff6'}
                     ]
                   )
                 }
               },
               data:data.nh
             },
             {
               name:'成本',
               type:'line',
               color: '#F2B44B',
               yAxisIndex: 1,
               data:data.cost
             },
             {
               name:'省级标准',
               type:'line',
               color: 'rgba(65,117,5,1)',
               data:data.provinciallevel
             },
             {
               name:'企业标准',
               type:'line',
               color: '#BD10E0',
               data:data.enterpriselevel
             }
           ]
         };
          var myChart = echarts.init(document.getElementById("chart_energy"));
          myChart.setOption(option);
      },
      btnclick(flag){
         this.btnflag=flag;
         this.search();

      }  
    },
  };


</script>

<style scoped>
  .div-top{
    height: 55px;
    border-bottom: 1px solid #d7d7d7;
  }
  .title{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0.19px;
    font-weight:bold;
  }
  .btn-group{
     float: right;
     margin-right: 20px;
  }
  .div-content{

  }
  .div-frist{
    margin-top: 20px;
  }
  .div-second{
    width: 100%;
    background-color: #F5F5F6;
    height:220px;
    margin-top:10px;
  }
  .div-third{
    margin-top: 20px;
    background-color: #F5F5F6;
    height:240px;
  }
  .scope-icon{
    float: left;
    width: 40px;
    height: 4px;
    background-color: #F5A623;
    border: 1px solid  #F5A623;
    margin-top: 10px;
  }
  .el-table th, .el-table tr {
    background-color: #F5F5F6;
    border: 1px solid #ffffff;
  }
  .el-table--enable-row-transition .el-table__body td{
    border: 1px solid #ffffff;
  }
</style>



